<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>日报管理</el-breadcrumb-item>
      <el-breadcrumb-item>查找日报</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
<!-- 搜索与添加区域-->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="请输入老师姓名" v-model="queryInfo.teacherName">
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="finName">搜索日报</el-button>
        </el-col>
        <el-col :span="7">
          <el-input placeholder="请输入登记时间" v-model="queryInfo.find">
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary">搜索日报</el-button>
        </el-col>
      </el-row>
      <!--日报列表区域-->
      <el-table :data="category" border stripe id="exportTab">
        <el-table-column label="ID" prop="id" sortable></el-table-column>
        <el-table-column label="姓名" prop="teacher_name"></el-table-column>
        <el-table-column label="所属学院" prop="department" width="150px"></el-table-column>
        <el-table-column label="教室" prop="class_room"></el-table-column>
        <el-table-column label="登记时间" prop="post_date" sortable></el-table-column>
        <el-table-column label="班级" prop="class" width="165px"></el-table-column>
        <el-table-column label="节次" prop="section"></el-table-column>
        <el-table-column label="实到人数" prop="true_attend_num"></el-table-column>
        <el-table-column label="应到人数" prop="attend_num"></el-table-column>
        <el-table-column label="使用手机人数" prop="use_media"></el-table-column>
        <el-table-column label="衣着问题" prop="un_image_student"></el-table-column>
        <el-table-column label="投影仪问题" prop="projector_damage"></el-table-column>
        <el-table-column label="电脑问题" prop="computer_damage"></el-table-column>
        <el-table-column label="教室卫生" prop="other_warn_damage"></el-table-column>
        <el-table-column label="其他问题" prop="other_things"></el-table-column>
        <el-table-column label="课程" prop="course"></el-table-column>
        <el-table-column label="班主任" prop="head_master"></el-table-column>
        <el-table-column label="旷课学生列表" prop="truancy_student" width="190px"></el-table-column>
        <el-table-column label="迟到学生列表" prop="late_student" width="190px"></el-table-column>
        <el-table-column label="早退学生列表" prop="leave_early_student" width="190px"></el-table-column>
        <el-table-column label="请假学生列表" prop="leave_student" width="190px"></el-table-column>
        <el-table-column label="旷课学生人数" prop="truancy_student_num"></el-table-column>
        <el-table-column label="迟到学生人数" prop="late_student_num"></el-table-column>
        <el-table-column label="早退学生人数" prop="leave_early_student_num"></el-table-column>
        <el-table-column label="请假学生人数" prop="leave_student_num"></el-table-column>
        <el-table-column label="教师编号" prop="teacher_id"></el-table-column>
        <el-table-column label="操作" width="120px">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修改日报"
                        placement="top-start" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xiugai(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除日报"
                        placement="top-start" :enterable="false">
              <el-button type="primary" icon="el-icon-delete" size="mini"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

<!--  分页    -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.page"
          :page-sizes="[1, 10, 20, 200]"
          :page-size="queryInfo.limit"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
      <el-button type="primary" @click="exportExcel">下载表格</el-button>
    </el-card>
  </div>
</template>

<script>
import FileSaver from 'file-saver'

import XLSX from 'xlsx'
export default {
  data(){
    return{
      queryInfo:{
        teacherName:'',
        page:1,
        limit:10
      },
      category:[],
      total:0
    }
  },
  created() {         //生命周期函数
    this.getUserList()
  },
  methods:{
    //查询全部数据
    async getUserList(){
     const {data:res}=await this.$http('daily/get_list/GetList',{
       params:this.queryInfo
     })
      console.log(res)
      if(res.code!==1)
        return this.$message.error('获取日报列表失败')
      this.category=res.data
      this.total=res.len
      console.log(res)
    },
    //根据姓名查询日报
    async finName(){
      const {data:res}=await this.$http('daily/get_teacher_daily_list/GetTeacherDailyList',{
        params:this.queryInfo
      })
      if(res.code!==1)
        return this.$message.error(('获取日报失败'))
      this.category = res.data
      this.total = res.len
      console.log(res)
    },
    handleSizeChange(newSize){
        console.log(newSize)
        this.queryInfo.limit=newSize
      this.getUserList()
    },
    handleCurrentChange(newPage){
      console.log(newPage)
      this.queryInfo.page=newPage
      this.getUserList()
    },

    //导出表格函数
    exportExcel () {
      /* generate workbook object from table */
      var xlsxParam = { raw: true } // 导出的内容只做解析，不进行格式转换
      var wb = XLSX.utils.table_to_book(document.querySelector('#exportTab'), xlsxParam)

      /* get binary string as output */
      var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '审核情况表.xlsx')
      } catch (e) {
        if (typeof console !== 'undefined') {
          console.log(e, wbout)
        }
      }
      return wbout
    },
    async xiugai(id){
      const {data:res}=await this.$http.delete(`/cat/${id}`)
      if(res.code!==0)
        return this.$message.error('获取日报列表失败')
      console.log(res)
    }
  }
}
</script>

<style scoped>

</style>