<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>查看用户</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="请输入老师姓名">
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary">搜索老师</el-button>
        </el-col>
      </el-row>
      <el-table :data="category" border stripe id="exportTab">
        <el-table-column label="ID" prop="id" sortable></el-table-column>
        <el-table-column label="姓名" prop="nickname"></el-table-column>
        <el-table-column label="编号" prop="username"></el-table-column>
        <el-table-column label="操作" width="120px">
          <template slot-scope>
            <el-tooltip class="item" effect="dark" content="修改老师" placement="top-start" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除老师" placement="top-start" :enterable="false">
              <el-button type="primary" icon="el-icon-delete" size="mini"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  分页    -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.page"
          :page-sizes="[1, 10, 20, 200]"
          :page-size="queryInfo.limit"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
  </div>

</template>

<script>
export default {
  data(){
    return{
      queryInfo:{
        find:'',
        page:1,
        limit:10
      },
      category:[],
      total:0
    }
  },
  created() {         //生命周期函数
    this.getUserList()
  },
  methods:{//查询全部数据
    async getUserList(){
      const {data:res}=await this.$http('daily/get_user/GetUser',{
        params:this.queryInfo
      })
      if(res.code!==1)
        return this.$message.error('获取教师列表失败')
      this.category=res.data
      this.total=res.len
      console.log(res)
    },
    handleSizeChange(newSize){
      console.log(newSize)
      this.queryInfo.limit=newSize
      this.getUserList()
    },
    handleCurrentChange(newPage){
      console.log(newPage)
      this.queryInfo.page=newPage
      this.getUserList()
    }
  }
}
</script>

<style scoped>

</style>