<template>
  <div class="login_con">
    <div class="login_box">
      <div class="avatar_box">
        <img src="../../assets/logo.png" alt="">
      </div>

      <!-- 表单登录-->
      <div>
        <el-form ref="loginref" :model="loginfor" :rules="loginRules" label-width="80px" class="loginform">
          <el-form-item label="账号" prop="number">
            <el-input v-model="loginfor.number" prefix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="loginfor.password" type="password" prefix-icon="el-icon-lock"></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="success">注册</el-button>
            <el-button type="info" @click="loginref">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      /* 这是登录表单数据绑定对象 */
      loginfor: {
        number: '',
        password: '',
        ram:''
      },
      /* 表单的验证规则对象 */
      loginRules: {
        number: [
          {required: true, message: '请输入账号', trigger: 'blur'},
          {min: 5, max: 10, message: '长度5-10字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 5, max: 100, message: '密码最低5位', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    //点击重置按钮
    toFixed() {
    },
    loginref() {
      this.$refs.loginref.resetFields();
    },
    //登录验证
    login() {
      this.$refs.loginref.validate(async valid => {
        if (!valid) return;
        const {data: check}= await this.$http("/daily/Random/Random")
        this.loginfor.ram=check.ram
        this.suibian=check.ram

        //console.log(check.ram)
        const {data: res} = await this.$http("/daily/Login/Login", {
          params:this.loginfor
        })

        if(res.code==check.ram)
        {
          console.log(res.token);
          window.sessionStorage.setItem("token", res.token);
          this.$router.push("/index");
        }
        else{
          console.log(res.code)
          console.log(check.ram)
          return this.$message.error(('登录失败，密码错误'))
        }

        });

      }
    }
};
</script>

<style Lang="less" scoped>
.login_con {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #FFF0FF;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.avatar_box {
  height: 130px;
  width: 130px;
  border: 1px solid #EEEEEE;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #DDDDDD;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #FFFFFF;

  img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background-color: #EEEEEE;
  }
}

.btns {
  display: flex;
  justify-content: flex-end;
}

.loginform {
  width: 80%;
  padding: 120px 10px;
  margin: 8px 0;
  display: inline-block;
  border-radius: 4px;
  box-sizing: border-box;
}


</style>
